import { Person } from './person.model';
import { HttpClientUploadService } from '@wkoza/ngx-upload';
var SimpleMaterialComponent = (function () {
    function SimpleMaterialComponent(uploader) {
        this.uploader = uploader;
        this.options = {
            color: 'dropZoneColorMaterial',
            colorDrag: 'dropZoneColorDragMaterial',
            colorDrop: 'dropZoneColorDropMaterial'
        };
    }
    SimpleMaterialComponent.prototype.ngOnInit = function () {
        this.uploader.queue = [];
        this.model = new Person();
        this.uploader.removeAllFromQueue();
        this.uploader.onCancel$.subscribe(function (data) {
            console.log('file deleted: ' + data.file);
        });
        this.uploader.onProgress$.subscribe(function (data) {
            console.log('upload file in progree: ' + data.progress);
        });
        this.uploader.onSuccess$.subscribe(function (data) {
            console.log("upload file successful:  " + data.item + " " + data.body + " " + data.status + " " + data.headers);
        });
    };
    SimpleMaterialComponent.prototype.activeRemoveAllBtn = function () {
        return this.uploader.queue.some(function (item) { return (item.isReady || item.isCancel || item.isError); });
    };
    SimpleMaterialComponent.prototype.activeUploadAllBtn = function () {
        return this.uploader.queue.some(function (item) { return (item.isReady); });
    };
    SimpleMaterialComponent.prototype.activeCancelAllBtn = function () {
        return this.uploader.queue.some(function (item) { return item.uploadInProgress; });
    };
    return SimpleMaterialComponent;
}());
export { SimpleMaterialComponent };



