import { Renderer2 } from '@angular/core';
import { Person } from './person.model';
import { HttpClientUploadService } from '@wkoza/ngx-upload';
import * as ts from 'typescript';
var SimpleBootstrapComponent = (function () {
    function SimpleBootstrapComponent(uploader, renderer) {
        this.uploader = uploader;
        this.renderer = renderer;
    }
    SimpleBootstrapComponent.prototype.ngOnInit = function () {
        console.log(ts.version);
        this.uploader.queue = [];
        this.model = new Person();
        this.uploader.onCancel$.subscribe(function (data) {
            console.log('file canceled: ' + data.file.name);
        });
        this.uploader.onProgress$.subscribe(function (data) {
            console.log('upload file in progree: ' + data.progress);
        });
        this.uploader.onSuccess$.subscribe(function (data) {
            console.log("upload file successful:  " + data.item + " " + data.body + " " + data.status + " " + data.headers);
        });
    };
    SimpleBootstrapComponent.prototype.makeThumbnail = function (item, el) {
        var reader = new FileReader();
        reader.onload = function (e) {
            // get loaded data and render thumbnail.
            //   document.getElementById("thumbnail").src = e.target.result;
            // this.renderer.setProperty(el,'src',e.target.result)
        };
        // read the image file as a data URL.
        reader.readAsDataURL(item.file);
    };
    SimpleBootstrapComponent.prototype.activeRemoveAllBtn = function () {
        return this.uploader.queue.some(function (item) { return (item.isReady || item.isCancel || item.isError); });
    };
    SimpleBootstrapComponent.prototype.activeUploadAllBtn = function () {
        return this.uploader.queue.some(function (item) { return (item.isReady); });
    };
    SimpleBootstrapComponent.prototype.activeCancelAllBtn = function () {
        return this.uploader.queue.some(function (item) { return item.uploadInProgress; });
    };
    return SimpleBootstrapComponent;
}());
export { SimpleBootstrapComponent };



